@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
}

.red-text, .red-link {
  color: #fa7470;
}

.red-link:hover {
  color: #df6763;
  text-decoration: none;
  cursor: pointer;
}

.btn-primary:focus, .btn-primary:disabled {
  border-color: #fa7470 !important;
}

button[id*="-btn"], a[id*="-btn"] {
  padding: 10px 15px;
  border-radius: 3px;
  font-size: 20px;
  margin: 15px 0;
  width: -webkit-max-content;
  width: max-content;
  border: 1px solid #fa7470;
}

#white-btn {
  background-color: #fff;
  color: #fa7470;
}

#red-btn {
  background-color: #fa7470;
  color: #fff;
}

#disable-btn,
#white-btn:hover,
#red-btn:hover {
  text-decoration: none;
  background-color: #df6763;
  color: #fff;
  border: 1px solid #df6763;
}

.bold-text {
  font-weight: 600;
}

.bg-img { 
  width: 100%;
}

.error-wrapper {
  display: -webkit-flex;
  display: flex;
  margin: 15px 0;
}

.error-msg {
  color: #fd4d47;
  font-size: smaller;
  margin: 0;
}

.error-icon {
  color: white;
  background-color: #fd4d47;
  font-size: x-small;
  width: 15px;
  height: 15px;
  text-align: center;
  border-radius: 50%;
  margin: 1px 4px 0 0;
  font-weight: bolder;
}

.blured {
  filter: blur(5px);
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
}

.modal-dialog .modal-content {
  font-family: 'Montserrat', sans-serif;
}

.loader-wrapper > img{
  width: 300px;
}

.loader-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: calc(100vh - 60px);
}

