.c-workout-card .card {
    padding: 0;
    height: 270px;
    box-shadow: 1px 1px 7px 2px #ccc;
}

.c-workout-card .card-body {
    position: relative;
    z-index: 1;
}

.c-workout-card .card-body::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url(/fitness-app/static/media/logo_black.b01e638e.png) no-repeat center center;
    background-size: contain;
    opacity: 0.07;
}

.c-workout-card .card-body .card-title {
    font-weight: 600;
}

.c-workout-card .card-body .card-text {
    font-size: 17px;
}

.c-workout-card .card div[class*="text-wrapper"] {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.c-workout-card div[class*="text-wrapper"] .card-text:first-child {
    font-weight: 600;
}

.c-workout-card div[class*="text-wrapper"] .card-text:last-child {
    text-align: right;
}

.c-workout-card .card .card-footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 0;
    border: none;
}

