    background-color: #bbb;
}

.c-workout-card .card-footer > .play-btn {
    background-color: #fa7470;
}

.c-workout-card .card-footer > .delete-btn:hover {
    background-color: #999;
}

.c-workout-card .card-footer > .play-btn:hover {
    background-color: #df6763;
}

.c-workout-card .card-footer > .play-btn > a {
    display: block;
    padding: 10px;
}


