.c-fitness-navbar .navbar-brand > img {
    width: 30px;
    margin: 0 7px 3px 0;
}

@media (min-width: 992px) {
    .c-fitness-navbar .dropdown-menu {
        top: 120%;
        border: none;
        border-radius: 0;
        box-shadow: 0 3px 15px #ccc;
    }
}

@media (max-width: 992px) {
    .c-fitness-navbar .dropdown-menu {
        text-align: center;
        border: none;
    }
}

.c-fitness-navbar .dropdown-item.active,
.c-fitness-navbar .dropdown-item:hover {
    background-color: transparent;
    color: inherit;
}

.c-fitness-navbar .navbar-light .navbar-nav .nav-link,
.c-fitness-navbar .navbar .red-text:hover {
    color: #333;
}

.c-fitness-navbar .navbar-light .navbar-nav .nav-link:hover,
.c-fitness-navbar .dropdown-item:hover {
    color: #fa7470;
}

.c-fitness-navbar .navbar-toggler {
    border: none;
}